.AppBarWrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid rgb(114, 71, 255);
    box-shadow: 0px 2px 4px -1px rgb(114, 71, 255),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    background-color: rgb(248, 193, 236);
    opacity: 90%;

}

